#page {
    display: grid;
    width: 100%;
    height: 250px;
    grid-template-areas: "head head"
                         "span  main"
                         "span  foot";
    grid-template-rows: 100px 35em 30px;
    grid-template-columns: 250px 1fr;
  }
  
  #page > header {
    grid-area: head;
    background-image: linear-gradient(45deg, #553c9a, #b393d3) ;
  }
  
  #page > span {
    grid-area: span;
  }
  
  #page > main {
    grid-area: main;
  }
  
  #page > footer {
    grid-area: foot;
    background-color: #8cffa0;
  }

  #hotel-details-card {
      display: grid;
      flex-direction: column;
      background-color: white;
      flex-basis: 30%;
      width: 90%;
      height: 50%;
      margin-left: 5%;
      margin-top: 2%;
      border: 1px solid grey;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 5px;
      position: relative;
  }
  
  #hotel-details-card > div {
      background-color: white;
      padding: 20px;
  }

  .hotel-descprition {
      font-size: 10px;
      text-align: justify;
      margin-top: 15px;
      text-overflow: clip;
  }

  .hotel-name { 
    color: #553c9a; 
    font-family: 'Lobster', Lucida; 
    font-size: 25px; 
    font-weight: normal;   
    text-shadow: 1px 0 0 #fff;
    text-align: left;
    text-overflow: clip;
}
 
.hotel-review-price-details {
    margin-top: 20px;
    text-align: right;
    position: relative;
}

.see-description {
    position: relative;
    margin-top: 10px;
    color: #b393d3;
    text-align: right;
}

.container {
    display: block;
    position: relative;
    padding-left: 40px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 15px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: left;
  }
  
  .container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    padding-left: 2px;
  }
  
  .container:hover input ~ .checkmark {
    background-color: #ccc;
  }
  
  .container input:checked ~ .checkmark {
    background-color: #553c9a;
  }
  
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  .container input:checked ~ .checkmark:after {
    display: block;
  }
  
  .container .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  .filter-container {
      padding: 10%;
      width: 250px;
      margin-top: 38%;
  }

  .filter-container-bg {
      background-image: linear-gradient(to bottom left, #553c9a, #b393d3);
      padding: 10%;
      color: white;
      border: 1px solid #b393d3;
  }

  h2, h4 {
      text-align: left;
  }

  .hotel-not-found {
    display: flex;
    flex-shrink: 0;
    margin-left: 20%;
    font-size: 50px;
    color: #8B0000;
    font-family: 'Fira Sans', serif;
    text-align: justify;
